import {Component, OnInit} from '@angular/core';
import {User} from '../../../../../common/src/lib/interfaces/user';
import {ProductService} from '../../services/product.service';
import {LinkService} from '../../services/link.service';
import {Auth} from '../../../../../common/src/lib/classes/auth';
import {environment} from '../../../environments/environment';
import {StatsService} from '../../services/stats.service';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  products = [];
  title = 'Welcome';
  description = 'Log in to share links and earn money!';
  user: User;
  selected = [];
  link = '';
  error = false;
  total = 0;

  constructor(
    private productService: ProductService,
    private linkService: LinkService,
    private statsService: StatsService
  ) {
  }

  ngOnInit(): void {
    this.productService.all().subscribe(
      (res: any) => {
        this.products = res.data;
      }
    );

    if (!Auth.user) {
      return;
    }

    this.user = Auth.user;
    this.title = '$0';
    this.description = 'You have earned in total';

    this.statsService.get().subscribe(
      (res: { revenue: number }[]) => {
        const revenue = res.reduce((s, o) => s + o.revenue, 0);
        this.title = '$' + revenue;
      }
    );
  }

  generate() {
    const data = {
      products: this.selected.map(p => p.id)
    };

    this.linkService.create(data).subscribe(
      (res: any) => {
        this.link = `${environment.url.checkout}/${res.data.code}`;
      },
      err => {
        this.error = true;
      }
    );
  }

  addProduct(id: number) {
    if (this.selected.filter(s => s === id).length !== 0) {
      this.selected = this.selected.filter(s => s !== id);
      return;
    }

    this.selected.push(id);
  }

  isSelected(id: number) {
    return this.selected.filter(s => s === id).length !== 0;
  }

  search(text: string) {
    this.productService.all({
      s: text
    }).subscribe(
      (res: any) => {
        this.products = res.data;
      }
    );
  }
}
